export const Agent = ({avtSrc, name}:{avtSrc:string, name:string}) => {
    const isSpeaking = true;
    return (
        <div className="flex flex-col items-center justify-center gap-5 overflow-hidden h-[400px] dark-gradient w-full rounded-lg">
            <div className="flex size-15 rounded-full p-5 blue-gradient relative border-2 border-white">
                
                {isSpeaking && (
                    <span className="absolute size-5/6 animate-ping rounded-full bg-primary-200 opacity-75"></span>
                )}
            </div>
            <h4>{name}</h4>
        </div>
    )
}
